<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item to="/home"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-alert title="" type="warning"></el-alert>
      <el-steps :space="200" :active="activeIndex - 0" finish-status="success" align-center>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
      </el-steps>
      <el-form
        :model="addForm"
        :rules="addFormRules"
        ref="addFormRef"
        label-width="100px"
        label-position="top"
      >
        <el-tabs
          :tab-position="'left'"
          v-model="activeIndex"
          :before-leave="beforeLeave"
          @tab-click="tabClicked"
        >
          <el-tab-pane label="" name="0">
            <el-form-item label="" prop="goods_name">
              <el-input type="text" v-model="addForm.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_price">
              <el-input type="number" v-model="addForm.goods_price"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_weight">
              <el-input type="number" v-model="addForm.goods_weight"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_number">
              <el-input type="number" v-model="addForm.goods_number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_cat">
              <el-cascader
                v-model="addForm.goods_cat"
                :options="cateList"
                :props="{ expandTrigger: 'hover',label:'cat_name',value:'cat_id' }"
                @change="handleChange"
              ></el-cascader>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="1">
            <el-form-item :label="item.attr_name" v-for="item in manyTableData" :key="item.attr_id">
              <el-checkbox-group v-model="item.attr_vals">
                <!--  -->
                <el-checkbox
                  :label="item1"
                  border
                  v-for="(item1,index) in item.attr_vals"
                  :key="index"
                ></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="2">
            <el-form-item :label="item.attr_name" v-for="item in onlyTableData" :key="item.id">
              <el-input v-model="item.attr_vals"></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="3">
            <el-upload
              class="upload-demo"
              action="http://127.0.0.1:8888/api/private/v1/upload"
              :headers="headers"
              :file-list="fileList"
              list-type="picture"
              :on-success="handleSuccess"
              :on-remove="handleRemove"
              :on-preview="handlePreview"
            >
              <el-button size="small" type="primary"></el-button>
              <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
            </el-upload>
          </el-tab-pane>
          <el-tab-pane label="" name="4">
            <quill-editor
              v-model="addForm.goods_introduce"
            ></quill-editor>
            <el-button type="primary" @click="add"></el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>

    <!--  -->
    <el-dialog title="" :visible.sync="previewVisible" width="50%">
      <img :src="previewPath" alt />
    </el-dialog>
  </div>
</template>
<script>
import _ from 'lodash'
export default {
  created() {
    this.getCateList();
  },
  data() {
    return {
      activeIndex: "0",
      addForm: {
        goods_name: "",
        goods_price: 0,
        goods_weight: 0,
        goods_number: 0,
        goods_cat: [1, 3, 6],
        pics: [],
        goods_introduce:'',
        attrs:[],//
      },
      addFormRules: {
        goods_name: [
          { required: true, message: "", trigger: "blur" }
        ],
        goods_price: [
          { required: true, message: "", trigger: "blur" }
        ],
        goods_weight: [
          { required: true, message: "", trigger: "blur" }
        ],
        goods_number: [
          { required: true, message: "", trigger: "blur" }
        ],
        goods_cat: [
          { required: true, message: "", trigger: "blur" }
        ]
      },
      cateList: [],
      manyTableData: [],
      onlyTableData: [],
      fileList: [],
      headers: {
        Authorization: window.sessionStorage.getItem("token")
      },
      previewVisible: false,
      previewPath: ""
    };
  },
  methods: {
    // 
    async getCateList() {
      const { data: res } = await this.$http.get("categories");
      this.cateList = res.data;
      console.log(this.cateList);
    },
    handleChange() {
      //
      if (this.addForm.goods_cat.length < 3) {
        this.addForm.goods_cat = [];
        this.$message.error("");
        return; //
      }
    },
    beforeLeave(activeName, oldActiveName) {
      console.log(activeName, oldActiveName);
      //3
      if (oldActiveName == "0" && this.addForm.goods_cat.length < 3) {
        this.$message.error("");
        return false; //tab
      }
    },
    async tabClicked() {
      console.log(this.addForm.goods_cat);
      if (this.activeIndex == "1") {
        //
        const { data: res } = await this.$http.get(
          `categories/${this.cateId}/attributes`,
          { params: { sel: "many" } }
        );
        res.data.forEach(item => {
          if (item.attr_vals == "") {
            item.attr_vals = [];
          } else {
            item.attr_vals = item.attr_vals.split(",");
          }
          // item.attr_vals = item.attr_vals == ""?[]:item.attr_vals.split(',')
        });
        this.manyTableData = res.data;
        console.log(this.manyTableData);
      } else if (this.activeIndex == "2") {
        const { data: res } = await this.$http.get(
          `categories/${this.cateId}/attributes`,
          { params: { sel: "only" } }
        );

        this.onlyTableData = res.data; //splitattr_vals
        console.log(this.onlyTableData);
      }
    },
    handleSuccess(res) {
      if (res.meta.status == 200) {
        this.addForm.pics.push({ pic: res.data.tmp_path });
      }
    },
    handleRemove(file) {
      console.log(file);
      //addFormpicsindex) --> splice
      var path = file.response.data.tmp_path;
      var index = this.addForm.pics.findIndex(item => item.pic == path);
      this.addForm.pics.splice(index, 1);
    },
    handlePreview(file) {
      console.log(file);
      this.previewPath = file.response.data.url;
      this.previewVisible = true;
    },
     add(){
      this.$refs.addFormRef.validate(async valid=>{
        if(!valid)return this.$message.error('')
        var form = _.cloneDeep(this.addForm)//
        form.goods_cat = this.addForm.goods_cat.join(',')
        // console.log(this.addForm)
        var arr = []//
        console.log(this.manyTableData)
        this.manyTableData.forEach(item=>{
          var obj = {attr_id:item.attr_id,attr_value:item.attr_vals.join(',')}
          arr.push(obj)
        })
        console.log(this.onlyTableData)
        this.onlyTableData.forEach(item=>{
          var obj = {attr_id:item.attr_id,attr_value:item.attr_vals}
          arr.push(obj)
        })
        form.attrs = arr
        const {data:res} =  await this.$http.post('goods',form)
        this.$message.success('')
        //
        this.$router.push('/goods')
      })
    }
  },
  computed: {
    cateId() {
      return this.addForm.goods_cat[2];
    }
  }
};
</script>
<style scoped  lang="less">
.el-steps {
  margin-top: 15px;
}
</style>

<style scoped>
/* https://vue-loader.vuejs.org/zh/guide/scoped-css.html#%E6%B7%B1%E5%BA%A6%E4%BD%9C%E7%94%A8%E9%80%89%E6%8B%A9%E5%99%A8 */
/* .el-steps >>> .el-step__title{
    font-size:12px !important;
} */

/* .el-steps /deep/ .el-step__title{
    font-size:12px !important;
} */
</style>

<style lang="less">
// el-steps 
// .el-step__title{
//     font-size:12px !important;
// }

.el-dialog {
  img{
    display: block;
    margin:0 auto;
  }
}
.ql-editor{
  height:300px !important;
}
</style>
